export const drawPasswordValidator = [
  { required: true, message: '提款码不能为空', trigger: 'blur'},
  { validator: (rule, value, callback) => {
    if (value === '') {
      callback(new Error('请输入提款码'));
    } else {
      if (!/^\d+$/.test(value)) {
        callback(new Error('提款码6位数字'));
      }else {
        if(value.length != 6) {
          callback(new Error('提款码为6位数字'));
        }
        callback();
      }
    }
  }, trigger: 'blur' },
]

