export function usernameValidator(rule, value, callback) {
  if(!/^[a-zA-Z0-9]{6,20}$/.test(value)) {
    callback(new Error('用户名长度必须6-20位，允许字符：字母、数字'));
  } else {
    callback();
  }
};

