<template>
    <div id="Qz">
        <div class="container">
            <div class="auth_login" :style="{ backgroundImage: 'url(' + loginBg + ')' }">
                <el-form :model="formData" status-icon :rules="formRules" ref="form" label-position="top">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="formData.username" clearable placeholder="请输入账号，长度不能小于6位"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" clearable v-model="formData.password" autocomplete="off" placeholder="请输入密码，长度不能小于6位"></el-input>
                    </el-form-item>
                    <el-form-item class="submit_item">
                        <el-button type="primary" class="login_btn" @click="submitForm" :loading="loading">账号注册/登录</el-button>
                        <!-- <div>
                            <router-link to="/android" target="_blank" v-if="app.isAndroid">APP下载</router-link>
                            <router-link to="/ios" v-if="app.isIOS">APP下载</router-link>
                        </div> -->
                    </el-form-item>
                    <!-- <div class="tips">
                        <span @click="pushPath('/auth/register')">注册账号</span>
                        <span @click="pushPath('/help')" style="float: right;">忘记密码？</span>
                    </div> -->
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { 
    passwordValidator,
    usernameValidator
} from '@/utils/validator'
import { imgFormatter } from '@/utils'

// 茄子
export default {
    name: 'Qz',
    components: {

    },

    data() {
        return {
            url: '',
            loading: false,
            formData: {
                username: '',
                password: '',
                code: '',
            },

            formRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
            },

            loginBg: imgFormatter('/images/2019/09/29/edaf827d-2b47-4c9d-a571-526ba36c23b4.jpg')
        }
    },

    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
		product() { return this.$store.getters.product },
		productId() { return this.$route.params.productId },
        isQzApp() {
            return !(typeof callNative == "undefined");
        },
        isIframeId() {
            return [10, 18].indexOf(Number(this.productId)) > -1
        },
		queryGameType() { return this.$route.query.gameType },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(this.$route.query.sign) {
                window.localStorage.setItem('sign', this.$route.query.sign);
            }
        },

        submitForm() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if(this.loading) return ;

                    var data = {
                        username: this.formData.username,
                        password: this.formData.password,
                        code: this.formData.code || window.localStorage.getItem('sign'),
                        sign: window.localStorage.getItem('sign'),
                    };

                    if(!data.sign) {
                        this.$message.error('缺少Token');
                        return ;
                    }
                    // console.log(data);return ;

                    data.callback = () => {
                        window.location.href = this.$router.resolve('/nav/3').href;
                    };

                    this.loading = true;
                    this.$store.dispatch('loginAndRegisterByUsername', data).then((json) => {
                        this.loading = false;
                        if(json.code == 200) {
                            // let winOpen = window.open("", "product");

                            // setTimeout(() => {
                            //     const link = this.$router.resolve('/redirect?pid=' + this.productId);
                            //     winOpen.location = link.href;
                            //     this.$router.push('/');
                            // }, 100);
                        }
                    })
                } else {
                    console.log('提交错误');
                    return false;
                }
            });
        },
    }
}
</script>

